// blog custom site-header
.site-header {
  border-top: 5px solid rgb(128, 86, 156);
  border-bottom: 1px solid #54008c;
  min-height: 55.95px;
  line-height: 54px;
  position: relative;
  background-color: #54008c;
}
.site-title {
  color: white;
  font-family: "Source Code Pro", "Menlo", "DejaVu Sans Mono", "Lucida Console", monospace;
}

.site-title,
.site-title:visited {
  // color: #fbff00;
  color: #fff;
}

.site-title:hover {
  color: #000000;
  background-color: #fff;
  text-decoration: none;
}

// Nav Heading
