  color: rgb(0, 0, 0);
  font-size: 14pt;
  font-family: "Source Code Pro", "Menlo", "DejaVu Sans Mono", "Lucida Console", monospace;
}

.site-nav .page-link:hover {
  color: #ffffff;
  background-color: #000000;
  border-color: #000000;
  text-decoration: none;
}

